@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes opacity0to0 {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@mixin font($size, $weight) {
  font-size: $size;
  font-weight: $weight;
}

@mixin absolute($top: initial, $right: initial, $bottom: initial, $left: initial, $width: 24px, $height: 24px) {
  position: absolute;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
  width: $width;
  height: $height;
}

@mixin flex($flex-direction: row, $justify-content: flex-start, $align-items: center) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify-content;
  align-items: $align-items;
}

@mixin responsiveWidth() {
  width: 1440px;
  margin: 0 auto;
  @media screen and (max-width: 1440px) {
    width: 100%;
    padding-right: 24px;
    padding-left: 24px;
  }
}

@mixin scrollBar($type: vertical, $wh: 6px, $track-color: $c2, $thumb-color: $c1) {
  &::-webkit-scrollbar {
    width: $wh;
    height: $wh;
  }
  &::-webkit-scrollbar-track {
    background: $track-color;
  }
  &::-webkit-scrollbar-thumb {
    background: $thumb-color;
    border-radius: 9999px;
  }
  @if ($type == vertical) {
    overflow-x: hidden;
    overflow-y: scroll;
  } @else {
    overflow-x: scroll;
    overflow-y: hidden;
  }
}
